/* Profile Screen - Modern Design */
.profile-screen {
  min-height: 100vh;
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  padding-top: 90px;
}

.profile-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}

/* Tab Navigation */
.profile-tabs {
  display: flex;
  background: white;
  border-radius: 15px;
  padding: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
  overflow-x: auto;
}

.tab-button {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 12px 20px;
  border: none;
  background: transparent;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
  color: #6b7280;
  white-space: nowrap;
}

.tab-button.active {
  background: linear-gradient(135deg, #8b5cf6 0%, #ec4899 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(139, 92, 246, 0.3);
  transform: translateY(-1px);
}

.tab-button:hover:not(.active) {
  background: #f3f4f6;
  color: #374151;
}

.tab-emoji {
  font-size: 1.2rem;
}

.tab-label {
  font-size: 0.875rem;
}

/* Profile Content */
.profile-content {
  background: white;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
}

/* Overview Tab */
.profile-overview {
  display: grid;
  gap: 30px;
}

.profile-header {
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 30px;
  align-items: start;
  padding: 30px;
  background: linear-gradient(135deg, #8b5cf6 0%, #ec4899 100%);
  border-radius: 20px;
  color: white;
  position: relative;
  overflow: hidden;
}

.profile-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grid" width="10" height="10" patternUnits="userSpaceOnUse"><path d="M 10 0 L 0 0 0 10" fill="none" stroke="white" stroke-width="0.5" opacity="0.1"/></pattern></defs><rect width="100" height="100" fill="url(%23grid)"/></svg>');
  z-index: 0;
}

.profile-header > * {
  position: relative;
  z-index: 1;
}

.profile-avatar-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.profile-avatar-large {
  position: relative;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: linear-gradient(135deg, #fbbf24 0%, #f59e0b 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  border: 4px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.profile-avatar-large img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.avatar-placeholder {
  font-size: 3rem;
  font-weight: 700;
  color: white;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
}

.avatar-edit-btn {
  position: absolute;
  bottom: 0;
  right: 0;
  background: #ef4444;
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(239, 68, 68, 0.4);
  transition: all 0.2s ease;
}

.avatar-edit-btn:hover {
  transform: scale(1.1);
  box-shadow: 0 6px 20px rgba(239, 68, 68, 0.6);
}

.profile-completion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.completion-circle {
  position: relative;
  width: 80px;
  height: 80px;
}

.completion-svg {
  width: 80px;
  height: 80px;
  transform: rotate(-90deg);
}

.completion-bg {
  fill: none;
  stroke: rgba(255, 255, 255, 0.2);
  stroke-width: 2;
}

.completion-progress {
  fill: none;
  stroke: #fbbf24;
  stroke-width: 2;
  stroke-linecap: round;
  transition: stroke-dasharray 0.6s ease;
}

.completion-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.2rem;
  font-weight: 700;
  color: white;
}

.completion-label {
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
}

.profile-info {
  flex: 1;
}

.profile-name-section {
  margin-bottom: 20px;
}

.profile-name,
.profile-name-input {
  font-size: 2.5rem;
  font-weight: 800;
  margin: 0 0 10px 0;
  color: white;
  background: transparent;
  border: none;
  outline: none;
}

.profile-name-input {
  border-bottom: 2px solid rgba(255, 255, 255, 0.3);
  padding: 5px 0;
}

.profile-title,
.profile-title-input {
  font-size: 1.25rem;
  color: rgba(255, 255, 255, 0.9);
  margin: 0;
  background: transparent;
  border: none;
  outline: none;
}

.profile-title-input {
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  padding: 5px 0;
}

.profile-details {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.detail-item {
  display: flex;
  align-items: center;
  gap: 10px;
  color: rgba(255, 255, 255, 0.9);
}

.detail-input {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  padding: 8px 12px;
  color: white;
  flex: 1;
}

.detail-input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.profile-actions {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
}

.edit-actions {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}

.btn-save, .btn-cancel, .btn-edit {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
}

.btn-save {
  background: #10b981;
  color: white;
  box-shadow: 0 4px 15px rgba(16, 185, 129, 0.3);
}

.btn-save:hover {
  background: #059669;
  transform: translateY(-1px);
  box-shadow: 0 6px 20px rgba(16, 185, 129, 0.4);
}

.btn-cancel {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.btn-cancel:hover {
  background: rgba(255, 255, 255, 0.3);
}

.btn-edit {
  background: rgba(255, 255, 255, 0.9);
  color: #8b5cf6;
  box-shadow: 0 4px 15px rgba(255, 255, 255, 0.3);
}

.btn-edit:hover {
  background: white;
  transform: translateY(-1px);
  box-shadow: 0 6px 20px rgba(255, 255, 255, 0.4);
}

/* Profile Sections */
.profile-section {
  background: #f8fafc;
  border-radius: 15px;
  padding: 25px;
  border: 1px solid #e5e7eb;
}

.profile-section h3 {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 20px 0;
  display: flex;
  align-items: center;
  gap: 10px;
}

.profile-bio,
.bio-textarea {
  color: #6b7280;
  line-height: 1.6;
  margin: 0;
}

.bio-textarea {
  width: 100%;
  border: 1px solid #d1d5db;
  border-radius: 10px;
  padding: 15px;
  resize: vertical;
  font-family: inherit;
}

.social-links {
  display: grid;
  gap: 15px;
}

.social-link {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  background: white;
  border-radius: 10px;
  border: 1px solid #e5e7eb;
}

.social-input {
  flex: 1;
  border: none;
  outline: none;
  background: transparent;
  color: #374151;
}

/* Skills Section */
.skills-section {
  display: grid;
  gap: 20px;
}

.skills-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.skills-header h3 {
  margin: 0;
  color: #1f2937;
  font-size: 1.25rem;
  font-weight: 700;
}

.btn-add-skill {
  display: flex;
  align-items: center;
  gap: 8px;
  background: linear-gradient(135deg, #8b5cf6 0%, #ec4899 100%);
  color: white;
  border: none;
  border-radius: 10px;
  padding: 10px 20px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.2s ease;
}

.btn-add-skill:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 20px rgba(139, 92, 246, 0.3);
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 15px;
}

.skill-card {
  background: white;
  border-radius: 15px;
  padding: 20px;
  border: 1px solid #e5e7eb;
  transition: all 0.2s ease;
  position: relative;
  overflow: hidden;
}

.skill-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(135deg, #8b5cf6 0%, #ec4899 100%);
}

.skill-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
}

.skill-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
}

.skill-name {
  font-size: 1.1rem;
  font-weight: 600;
  color: #1f2937;
}

.skill-remove {
  background: none;
  border: none;
  color: #ef4444;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s ease;
}

.skill-remove:hover {
  background: #fef2f2;
}

.skill-level {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.skill-badge {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 600;
  color: white;
}

.skill-experience {
  font-size: 0.875rem;
  color: #6b7280;
}

/* Achievements Section */
.achievements-section {
  display: grid;
  gap: 20px;
}

.achievements-section h3 {
  margin: 0;
  color: #1f2937;
  font-size: 1.25rem;
  font-weight: 700;
}

.achievements-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.achievement-card {
  background: white;
  border-radius: 15px;
  padding: 25px;
  border: 2px solid #e5e7eb;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.achievement-card.earned {
  border-color: #10b981;
  background: linear-gradient(135deg, #ecfdf5 0%, #f0fdf4 100%);
}

.achievement-card.locked {
  opacity: 0.6;
  background: #f9fafb;
}

.achievement-card.earned::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
}

.achievement-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
}

.achievement-icon {
  font-size: 3rem;
  margin-bottom: 15px;
  text-align: center;
}

.achievement-info {
  text-align: center;
}

.achievement-name {
  font-size: 1.2rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 8px 0;
}

.achievement-description {
  color: #6b7280;
  margin: 0 0 10px 0;
  line-height: 1.5;
}

.achievement-date {
  font-size: 0.875rem;
  color: #10b981;
  font-weight: 500;
}

.achievement-badge {
  position: absolute;
  top: 15px;
  right: 15px;
  background: #10b981;
  color: white;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Activity Section */
.activity-section {
  display: grid;
  gap: 20px;
}

.activity-timeline {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-height: 400px;
  overflow-y: auto;
  padding-right: 10px;
}

.activity-item {
  display: flex;
  gap: 15px;
  padding: 20px;
  background: white;
  border-radius: 15px;
  border: 1px solid #e5e7eb;
  transition: all 0.2s ease;
}

.activity-item:hover {
  transform: translateX(5px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.activity-icon {
  font-size: 1.5rem;
  width: 50px;
  height: 50px;
  background: linear-gradient(135deg, #8b5cf6 0%, #ec4899 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.activity-content {
  flex: 1;
}

.activity-action {
  font-weight: 600;
  color: #1f2937;
  margin: 0 0 5px 0;
}

.activity-date {
  font-size: 0.875rem;
  color: #6b7280;
}

/* Settings Section */
.settings-section {
  display: grid;
  gap: 30px;
}

.settings-group {
  background: #f8fafc;
  border-radius: 15px;
  padding: 25px;
  border: 1px solid #e5e7eb;
}

.settings-group h4 {
  font-size: 1.125rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 20px 0;
  display: flex;
  align-items: center;
  gap: 10px;
}

.settings-options {
  display: grid;
  gap: 15px;
}

.setting-option {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 15px;
  background: white;
  border-radius: 10px;
  border: 1px solid #e5e7eb;
  cursor: pointer;
  transition: all 0.2s ease;
}

.setting-option:hover {
  background: #f8fafc;
  border-color: #8b5cf6;
}

.setting-option input[type="checkbox"],
.setting-option input[type="radio"] {
  width: 18px;
  height: 18px;
  accent-color: #8b5cf6;
}

.setting-option span {
  font-weight: 500;
  color: #374151;
}

/* Empty States */
.empty-state {
  text-align: center;
  padding: 60px 20px;
  background: #f8fafc;
  border-radius: 15px;
  border: 2px dashed #d1d5db;
}

.empty-icon {
  font-size: 4rem;
  margin-bottom: 20px;
  opacity: 0.5;
}

.empty-state h4 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 10px 0;
}

.empty-state p {
  color: #6b7280;
  margin: 0 0 25px 0;
}

.btn-primary {
  background: linear-gradient(135deg, #8b5cf6 0%, #ec4899 100%);
  color: white;
  border: none;
  border-radius: 10px;
  padding: 12px 24px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn-primary:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 20px rgba(139, 92, 246, 0.3);
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
}

.modal {
  background: white;
  border-radius: 20px;
  width: 90%;
  max-width: 500px;
  max-height: 90vh;
  overflow: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 25px 0 25px;
  border-bottom: 1px solid #e5e7eb;
  margin-bottom: 25px;
}

.modal-header h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0;
  padding-bottom: 15px;
}

.modal-close {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #6b7280;
  padding: 5px;
  border-radius: 5px;
  transition: all 0.2s ease;
}

.modal-close:hover {
  background: #f3f4f6;
  color: #374151;
}

.modal-body {
  padding: 0 25px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-weight: 600;
  color: #374151;
  margin-bottom: 8px;
}

.form-input,
.form-select {
  width: 100%;
  padding: 12px 15px;
  border: 1px solid #d1d5db;
  border-radius: 10px;
  font-size: 1rem;
  transition: all 0.2s ease;
  background: white;
}

.form-input:focus,
.form-select:focus {
  outline: none;
  border-color: #8b5cf6;
  box-shadow: 0 0 0 3px rgba(139, 92, 246, 0.1);
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 15px;
  padding: 25px;
  border-top: 1px solid #e5e7eb;
  margin-top: 25px;
}

.btn-secondary {
  background: white;
  color: #374151;
  border: 1px solid #d1d5db;
  border-radius: 10px;
  padding: 10px 20px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn-secondary:hover {
  background: #f9fafb;
  border-color: #9ca3af;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .profile-header {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 20px;
  }
  
  .skills-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
  
  .achievements-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
}

@media (max-width: 768px) {
  .profile-container {
    padding: 15px;
  }
  
  .profile-content {
    padding: 20px;
  }
  
  .profile-tabs {
    padding: 6px;
  }
  
  .tab-button {
    padding: 10px 15px;
    font-size: 0.875rem;
  }
  
  .tab-label {
    display: none;
  }
  
  .profile-header {
    padding: 20px;
  }
  
  .profile-name,
  .profile-name-input {
    font-size: 2rem;
  }
  
  .skills-grid,
  .achievements-grid {
    grid-template-columns: 1fr;
  }
  
  .modal {
    width: 95%;
  }
}

@media (max-width: 480px) {
  .profile-avatar-large {
    width: 100px;
    height: 100px;
  }
  
  .avatar-placeholder {
    font-size: 2.5rem;
  }
  
  .completion-circle {
    width: 60px;
    height: 60px;
  }
  
  .completion-svg {
    width: 60px;
    height: 60px;
  }
  
  .completion-text {
    font-size: 1rem;
  }
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.profile-content {
  animation: fadeInUp 0.6s ease forwards;
}

.skill-card,
.achievement-card,
.activity-item {
  animation: fadeInUp 0.4s ease forwards;
}

/* Loading states */
.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  z-index: 10;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #e5e7eb;
  border-top: 4px solid #8b5cf6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
